import { NxTabGroupBase } from './tab-group-base';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
  Optional,
  Inject,
  Host
} from '@angular/core';
import { Subject } from 'rxjs';

@Component({
  selector: 'nx-tab',
  exportAs: 'nxTab',
  templateUrl: 'tab.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NxTabComponent implements OnInit, OnChanges, OnDestroy {
  private _label: string;

  /** The portal that will be the hosted content of the tab */
  private _contentPortal: TemplatePortal | null = null;

  /** Emits whenever the internal state of the tab changes. */
  readonly _stateChanges = new Subject<void>();

  /** Sets the label of the tab shown in the tablist. */
  @Input()
  get label(): string {
    return this._label;
  }
  set label(value: string) {
    if (this._label !== value) {
      this._label = value;
    }
  }

  /** @docs-private */
  get content(): TemplatePortal | null {
    return this._contentPortal;
  }

  /** Template inside the NxTab view that contains an `<ng-content>`. */
  @ViewChild(TemplateRef) _implicitContent: TemplateRef<any>;

  /**
   * Whether the tab is currently active.
   */
  isActive = false;

  constructor(
    private _viewContainerRef: ViewContainerRef,
    @Inject(NxTabGroupBase) @Optional() @Host() private _tabGroup: NxTabGroupBase) {
    if (!this._tabGroup) {
      throw Error(`The nx-tab element has to be wrapped in a nx-tab-group to work.
      Please provide a nx-tab-group element and place your tabs inside it.`);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.hasOwnProperty('label')) {
      this._stateChanges.next();
    }
  }

  ngOnInit() {
    this._contentPortal = new TemplatePortal(this._implicitContent, this._viewContainerRef);
  }

  ngOnDestroy(): void {
    this._stateChanges.complete();
  }
}
